function importFont() {
  const styleElement = document.createElement('style');
  document.head.appendChild(styleElement);
  const styleSheet = styleElement.sheet as CSSStyleSheet;
  return function insertRule(style: string | FontObject) {
    if (!styleSheet) {
      return;
    }

    if (typeof style === 'string') {
      styleSheet.insertRule(style, 0);
      return;
    }

    if (typeof style === 'object') {
      const { family, source, descriptors } = style;
      const rule = `
        @font-face {
          font-family: ${family};
          font-style: ${descriptors.style};
          font-weight: ${descriptors.weight};
          src: ${source};
        }
      `;
      styleSheet.insertRule(rule, 0);
    }
  };
}

export const dynamicalFontImport = importFont();
